@keyframes attack {
    0%
    {
        background-position: 0px 100px;    
    }
    20%
    {
        background-position: -500px 100px;    
    }
    25%
    {
        background-position: -1000px 100px;    
    }
    30%
    {
        background-position: -1500px 100px;    
    }
    35%
    {
        background-position: -2000px 100px;    
    }
    40%
    {
        background-position: -2500px 100px;    
    }
    45%
    {
        background-position: -3000px 100px;    
    }
}

@keyframes take-damage {
    50% 
    {
        background-position: -3000px 100px;
    }
    55% 
    {
        background-position: -5500px 100px;
    }
    60% 
    {
        background-position: -5000px 100px;
    }
    65% 
    {
        background-position: -5500px 100px;
    }
    70% {
        background-position: -3000px 100px;
    }
}

@keyframes take-final {
    50% 
    {
        background-position: -3000px 100px;
    }
    55% 
    {
        background-position: -5500px 100px;
    }
    60% 
    {
        background-position: -5000px 100px;
    }
    65% 
    {
        background-position: -6500px 100px;
    }
    70% {
        background-position: -7000px 100px;
    }
    75% {
        background-position: -7500px 100px;
    }
    80% {
        background-position: -8000px 100px;
    }
    85% {
        background-position: -8500px 100px;
    }
    100% {
        background-position: -8500px 100px;
    }
}

@keyframes bird-fly {
    0%
    {
        top: 125px;
        left: 195px;
    }
    19.9% 
    {
        top: 195px;
        left: 65px;
    }
    20%
    {
        top: 195px;
        left: 65px;
    }
    24.9% 
    {
        top: 125px;
        left: 195px;
    }
    25%
    {
        top: 125px;
        left: 195px;
        transform: rotateZ(30deg);
    }
    29.9% 
    {
        top: 50px;
        left: 340px;
        transform: rotateZ(30deg);
    }
    30%
    {
        top: 50px;
        left: 340px;
        transform: rotateZ(90deg);
    }
    50%
    {
        top: 140px;
        left: calc(100vw - 350px - 200px);
        transform: rotateZ(720deg);
    }
    70%
    {
        top: -200px;
        left: calc(100vw);
    }
}

@keyframes rock-fly {
    0%
    {
        top: 135px;
        left: 315px;
    }
   
    10%
    {
        top: 50px;
        left: 450px;
        transform: rotateZ(90deg);
    }
    50%
    {
        top: 100px;
        left: calc(100vw - 350px - 150px);
        transform: rotateZ(720deg);
    }
    70%
    {
        top: -200px;
        left: calc(100vw);
    }
}

.attack-anim {
    animation: attack 3s steps(1);
}

.take-damage-anim {
    animation: take-damage 3s steps(1);
}

.take-damage-final {
    animation: take-final 3s forwards steps(1);
}

.bird-fly {
    animation: bird-fly 3s linear;
}

.rock-fly {
    animation: rock-fly 3s linear;
}