.task-page {
    position: absolute;
    background-color: rgba(224, 157, 55, 0.842);
    border: 3px solid white;
    border-radius: 5px;
    top: 30%;
    left: 15%;
    width: 70%;
    height: 60%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    z-index: 1000;
}

.correct-answer {
    position: absolute;
    top: 0;
    font-size: 40px;
    color: greenyellow;
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    text-shadow: 0px 4px 14px #000000;
}

.wrong-answer {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    font-size: 40px;
    color:red;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2000;
    text-shadow: 0px 4px 14px #000000;
}

.check-result-button {
    margin-top: 30px;
    font-size: 20px;
}

.task-page input {
    border: 1px solid grey;
    padding: 5px;
    text-align: center;
}

.task-page p {
    margin: 20px auto;
}

.task-wrapper {
    width: 100%;
    text-align: center;
}

.comparision input {
    width: 40px;
}