.logo-wrapper {
    display: flex;
    justify-content: center;
    width: 800px;
    margin: 0 auto;
}

.logo-wrapper > div {
    width: 180px;
    height: 180px;
    border-radius: 5px;
    background-position: center;
    background-size: 90%; 
    background-repeat: no-repeat;
    margin: 20px auto;
    transition: 0.5s;
    cursor: pointer;
}

.logo-wrapper > div:hover {
    border: 3px solid white;
    transform: scale(1.1);
}

.selected-logo {
    border: 3px solid wheat;
}

