.battle-field { 
    position: relative;
    background-color: white;
    width: 100vw;
    height: 100vh;
    background-position: bottom center;
    background-size: 100% 100%; 
    overflow: hidden;
}

.steppe-location {
    background: url(assets/images/background/bg1.png) no-repeat;
    background-size: 100% 100%; 
}

.jungle-location {
    background: url(assets/images/background/bg5.jpg) no-repeat;
    background-size: 100% 100%; 
}

.battle-field-header {
    display: flex;
    justify-content: space-between;
    margin: 20px 40px;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: bold;
}

.player-name {
    width: 40%;
}

.opponent-name {
    width: 40%;
    text-align: right;
}

.player-container {
    position: absolute;
    bottom: 100px;
    left: 100px;
    width: 600px;
    height: 350px;
}

.opponent-container {
    position: absolute;
    bottom: 100px;
    right: 100px;
    width: 600px;
    height: 350px;
    transform: scale(-1, 1)
}

.health-bar {
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
}

.bar {
    height: 50px;
    width: 480px;
    border: 3px solid black;
    border-radius: 5px;
}

.opponent-health-bar {
    margin-left: 40px;
    transform: scale(-1, 1);
}

.throwableBird {
    width: 30px;
    height: 30px;
    position: absolute;
    top: -100px;
    left: calc(100vw + 100vh);
    background-image: url(assets/images/player/flying_red.png);
    background-size: 100%;
    background-repeat: no-repeat;
    z-index: 100;
}

.throwableRock {
    width: 30px;
    height: 30px;
    position: absolute;
    top: -100px;
    left: calc(100vw + 100vh);
    background-image: url(assets/images/player/flying_rock.png);
    background-size: 100%;
    background-repeat: no-repeat;
    z-index: 100;
}


.attack-button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px auto;
    width: 130px;
    text-align: center;
    padding: 10px;

}


.attack-button:hover {
    transform: scale(1.1);
    animation: none;
    transition: 0.3s;
}


.buttons-block button {
    margin: 0 30px;
}

.score-container {
    width: 60%;
    text-align: center;
    margin-bottom: 30px;
}

.score-container ol li {
    padding: 10px;
    border-bottom: 1px solid #000000;
}

.score-container ol li:last-child {
    border-bottom: none;
}

.score-item {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
}
